import * as React from 'react'
import Layout from '@components/Layout'
import Img from 'gatsby-image'
import styled from 'styled-components'
import { color, space } from 'styled-system'
import {
  MdFileDownload as _MdFileDownload,
  MdPhoto as _MdPhoto
} from 'react-icons/md'

import UI, { Flex, Box, Link } from '@components/UI'
import { Icon } from '@components/Social'

const MdFileDownload = styled(_MdFileDownload)`
  padding-top: 2px;
  ${color};
`
const MdPhoto = styled(_MdPhoto)`
  ${color}
`

const H4 = styled(UI.H4).attrs({})``

// STODO add types using graphql generated types
type PhotoProps = {
  pageContext: {
    childImageSharp: any
    photoInfo?: { link?: string; photographer?: string }
  }
}

const Photo = ({
  pageContext: {
    childImageSharp,
    photoInfo: { link, photographer } = { link: null, photographer: null }
  }
}: PhotoProps) => {
  return (
    <Layout goBackTo="photos" mt={2}>
      <Flex flexDirection="column">
        <Flex pb={[2, 3]} flexDirection="column">
          {photographer ? (
            <Flex direction="row">
              <MdPhoto color="dark" />
              <H4 color="dark" pb={1}>
                {'Photo by: '}
                <Link href={link}>{photographer}</Link>
              </H4>
            </Flex>
          ) : null}
          <Link href={childImageSharp.fluid.src} download>
            <Flex>
              <MdFileDownload color="dark" />
              <H4>Download (High Resolution)</H4>
            </Flex>
          </Link>
        </Flex>
        <Img
          alt="STODO"
          key={childImageSharp.fluid.src}
          fluid={childImageSharp.fluid}
        />
      </Flex>
    </Layout>
  )
}

export default Photo
